
:root {
  --send-bg: #0B93F6;
  --send-color: white;
  --receive-bg: #E5E5EA;
  --receive-text: black;
  --page-background: white;
}



.reveal{
  position: relative;
  transform: translateY(150px);
  opacity: 0;
  transition: 1s all ease;
}

.reveal.active{
  transform: translateY(0);
  opacity: 1;
}


.container {
  height: 3000px;
  padding-top: 100vh;
}


body {
  font-family: "Helvetica Neue", Helvetica, sans-serif;
  font-size: 20px;
  font-weight: normal;
  max-width: 450px;
  margin: 50px auto;
  display: flex;
  flex-direction: column;
  background-color: var(--page-background);
}

p {
  max-width: 255px;
  word-wrap: break-word;
  margin-bottom: 12px;
  line-height: 24px;
  position: relative;
  padding: 10px 20px;
  border-radius: 25px;
}

:before, :after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 25px;
}


.send {
  color: var(--send-color);
  background: var(--send-bg);
  align-self: flex-end;
}

:before {
  right: -7px;
  width: 20px;
  border-bottom-left-radius: 16px 14px;
}

:after {
  right: -26px;
  width: 26px;
  background-color: var(--page-background);
  border-bottom-left-radius: 10px;
}


.receive {
  background: var(--receive-bg);
  color: black;
  align-self: flex-start;
}

.receive:before {
  left: -7px;
  width: 20px;
  background-color: var(--receive-bg);
  border-bottom-right-radius: 16px 14px;
}

.send:before {
  right: -7px;
  width: 20px;
  background-color: var(--send-bg);
  border-bottom-right-radius: 16px 14px;
}

.receive:after {
  left: -26px;
  width: 26px;
  background-color: var(--page-background);
  border-bottom-right-radius: 10px;
}

#img.receive {
  padding: 20px;
  border-radius: 10px;
}

#link.receive {
  padding-top: 100px;
  border-radius: 10px;
  padding-left: 50px;
  padding-right: 50px;
  color: #0b93f6;
}

.dots.active {
}


.dot {
  height: 8px;
  width: 8px;
  margin: 1px;
  backround-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  -webkit-animation: ellipsis steps(4,end) 900ms infinite;
  animation: ellipsis steps(3,end) 900ms infinite;
}

@keyframes ellipsis {
  from {
    background-color: #bbb;
  }
  50% {
    background-color: #727272;
  }
  to {
    background-color: #6d6d6d;
  }
}

.gentle.active {
  animation: gentle;
  animation-duration: 3s;
}

.loud.active {
  animation: loud;
  animation-duration: 3s;
}


@keyframes gentle {
  from {
    padding: 10px 20px;
    font-size: 20px ;
  }

  50% {
    padding: 20px 30px;
    font-size: 15px;

  }

  to {
    padding: 10px 20px;
    font-size: 20px;

  }
}

